<template>
	<div id="WRAPPER">
    <h1 style="font-size: 18px; color: teal; text-align: center;">CALCULADORA DE <br> INDICE DE MASA CORPORAL</h1>
		<div class="datos_Usuario">
			<div class="datos">
				<label for="">Peso (kg)</label>
				<input type="number" v-model="peso" />
				<label for="">Altura (cm)</label>
				<input type="number" v-model="altura" />
			</div>
			<div class="action">
				<button @click="IMC">Calcular IMC</button>
			</div>
		</div>

		<div class="valoracion_Datos">
			<div class="table">
				<h2>¿Cómo se interpreta el IMC para adultos?</h2>
				<p>
					Para adultos de 20 años o más, el IMC se interpreta usando categorías de
					estado de peso estándar. Estas categorías son iguales para hombres y
					mujeres de todos los tipos de cuerpo y edades.
					<br />
					<br />
					Las categorías de estado de peso estándar asociadas con los rangos de IMC
					para adultos se muestran en la siguiente tabla.
				</p>
				<div class="grid">
					<div class="imc">IMC</div>
					<div class="peso">Peso</div>
					<div class="imc_pts">
						<span class="bajoPeso">Por debajo de 18.5</span>
						<br />
						<span class="normalPeso">18.5 a 24.9</span>
						<br />
						<span class="sobrePeso">25.0 a 29.9</span>
						<br />
						<span class="Obesidad">30.0 o más</span>
						<br />
					</div>
					<div class="imc_estado">
						<span class="bajoPeso">Bajo peso</span>
						<br />
						<span class="normalPeso">Normal</span>
						<br />
						<span class="sobrePeso">Sobrepeso</span>
						<br />
						<span class="Obesidad">Obesidad</span>
						<br />
					</div>
				</div>
			</div>
			<p id="tuPeso">
				Tu peso es:
				<b>{{ peso }} kg</b>
			</p>
			<p id="tuAltura">
				Tu altura es:
				<b>{{ altura }} cm</b>
			</p>
			<p id="tuIMC">
				Tu IMC (Indice de Masa Corporal) es:
				<br />
				<b>{{ pesoIdeal }}</b>
			</p>
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			peso: 0,
			altura: 0,
			pesoIdeal: 0
		};
	},
	methods: {
		IMC() {
			this.pesoIdeal = this.peso / (Math.pow(this.altura, 2) / 10000);

			if (this.peso === 0 || this.altura === 0 || this.pesoIdeal === 10000) {
				alert("Coloca tu peso y altura para poder calcular tu IMC");
				this.pesoIdeal = "No fue posible calcular tu IMC";
			} else {
				document.querySelectorAll("span").forEach((sp) => {
					sp.classList.remove("mark");
				});

				this.markObeso();
				this.markSobre();
				this.markNormal();
				this.markBajo();
			}
		},
		markObeso() {
			let obeso = document.querySelectorAll(".Obesidad");
			if (this.pesoIdeal > 30) {
				obeso.forEach((el) => {
					el.classList.add("mark");
				});
			}
		},
		markSobre() {
			let sobre = document.querySelectorAll(".sobrePeso");
			if (this.pesoIdeal >= 25 && this.pesoIdeal < 30) {
				sobre.forEach((el) => {
					el.classList.add("mark");
				});
			}
		},
		markNormal() {
			let normal = document.querySelectorAll(".normalPeso");
			if (this.pesoIdeal > 18.5 && this.pesoIdeal < 25) {
				normal.forEach((el) => {
					el.classList.add("mark");
				});
			}
		},
		markBajo() {
			let bajo = document.querySelectorAll(".bajoPeso");
			if (this.pesoIdeal < 18.5) {
				bajo.forEach((el) => {
					el.classList.add("mark");
				});
			}
		}
	}
};
</script>

